.information-panel {
  background-color: #f8f8f8;
  border: 0.125rem solid #e5e5e5;
  border-radius: 0.375rem;
  margin-bottom: 18px;
  opacity: 1;
  padding: 1.3125rem 1.125rem;
  position: relative;
}

.information-panel .icon {
  height: 1.375rem;
  left: 1.1875rem;
  line-height: 1;
  position: absolute;
  top: 1.1875rem;
  width: 1.375rem;
}

.information-panel.information-panel--success {
  background-color: rgba(112, 164, 0, 0.1);
  border-color: #70a400;
  padding: 1.3125rem 1.125rem 1.3125rem 3.75rem;
}

.information-panel.information-panel--success .icon {
  color: #70a400;
}

.information-panel.information-panel--success .information-panel__close {
  color: #70a400;
}

.information-panel.information-panel--success .information-panel__close:hover, .information-panel.information-panel--success .information-panel__close:focus {
  color: #598300;
}

.information-panel.information-panel--error {
  background-color: rgba(221, 56, 23, 0.1);
  border-color: #dd3817;
  padding: 1.3125rem 1.125rem 1.3125rem 3.75rem;
}

.information-panel.information-panel--error .icon {
  color: #dd3817;
}

.information-panel.information-panel--error .information-panel__close {
  color: #dd3817;
}

.information-panel.information-panel--error .information-panel__close:hover, .information-panel.information-panel--error .information-panel__close:focus {
  color: #b02c12;
}

.information-panel.information-panel--warning {
  background-color: rgba(255, 185, 0, 0.1);
  border-color: #ffb900;
  padding: 1.3125rem 1.125rem 1.3125rem 3.75rem;
}

.information-panel.information-panel--warning .icon {
  color: #ef7013;
}

.information-panel.information-panel--warning .information-panel__close {
  color: #ef7013;
}

.information-panel.information-panel--warning .information-panel__close:hover, .information-panel.information-panel--warning .information-panel__close:focus {
  color: #d1610e;
}

.information-panel.information-panel--info {
  background-color: rgba(0, 158, 222, 0.1);
  border-color: #009ede;
  padding: 1.3125rem 1.125rem 1.3125rem 3.75rem;
}

.information-panel.information-panel--info .icon {
  color: #009ede;
}

.information-panel.information-panel--info .information-panel__close {
  color: #009ede;
}

.information-panel.information-panel--info .information-panel__close:hover, .information-panel.information-panel--info .information-panel__close:focus {
  color: #006e9b;
}

.information-panel.close {
  border: 0 solid #e5e5e5;
  height: 0 !important;
  margin-bottom: 0;
  margin-top: 0;
  opacity: 0;
  overflow: hidden;
  padding-bottom: 0;
  padding-top: 0;
  transition: height 0.3s ease, padding 0.3s ease, margin 0.3s ease, border 0.3s ease, opacity 0.3s ease, border 0.3s ease;
}

.information-panel.close .information-panel__title {
  padding-top: 1.3125rem;
  transition: padding 0.3s ease;
}

.information-panel.information-panel--dismissible {
  padding-right: 2.5rem;
}

.information-panel__close {
  border: 0;
  height: 3.125rem;
  line-height: 1;
  padding: 0.8125rem;
  position: absolute;
  right: 0;
  top: 0;
  width: 3.125rem;
}

.information-panel__close .icon {
  height: 100%;
  left: auto;
  position: relative;
  top: auto;
  width: 100%;
}

.information-panel__close:hover {
  border: 0;
}

.information-panel__title {
  font-size: 0.9375rem;
  line-height: 1.125rem;
  margin-bottom: 0.375rem;
  padding-top: 0;
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600;
}

.information-panel--dismissible .information-panel__title {
  padding-right: 2.5rem;
}

.information-panel__content {
  font-size: 0.875rem;
  line-height: 1.125rem;
  margin-bottom: 0.1875rem;
}

.information-panel__content * {
  font-size: 0.875rem;
  line-height: 1.125rem;
}

.information-panel__link {
  border-color: #333f48;
  color: #333f48;
  font-size: 0.875rem;
  line-height: 1.125rem;
}

.information-panel__link:hover, .information-panel__link:focus, .information-panel__link:active, .information-panel__link:visited {
  border: 0;
  color: #333f48;
}

@media print, screen and (min-width: 48em) {
  .information-panel.information-panel--success, .information-panel.information-panel--error, .information-panel.information-panel--warning, .information-panel.information-panel--info {
    padding-right: 2.5rem;
  }
}
